.resumeSection{
    position: relative;
    top: 10vh;
}

.resumeContainer{
    position: relative;
}

.downloadButtonA{
    width: 60px;
    height: 60px;
    border-radius: 50%;
    position: relative;
    left: 85%;
    margin: 32px 0;
    font-size: 2rem;
    text-align: center;
    line-height: 60px;
    background: linear-gradient(180deg, rgba(0, 163, 255, .05) 0%, rgba(0, 163, 255, .019999999552965164) 100%);
    backdrop-filter: blur(0px);
    box-shadow: 0px 0px 4px var(--primary);
}

.downloadButtonA a{
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    cursor: pointer;
}

.downloadButtonA:hover{
    box-shadow: 0px 1px 20px 0px var(--primary);
}

.resumeSection object{
    position: relative;
    left: 50%;
    transform: translate(-50%);
    background-color: white;
}

@media screen and (max-width: 900px) {
    object{
        width: 90vw;
    }
    .downloadButtonA{
        display: none;
    }
    .resumeSection{
        top: 15vh;
    }
}

.downloadButtonB{
    justify-content: center;
    display: flex;
}

.downloadButtonB .downloadButton{
    display: block;
    margin: 20px 0;
    width: 250px;
    height: 40px;
    text-align: center;
    line-height: 40px;
    cursor: pointer;
    background-color: white;
    text-decoration: none;
}

.downloadButton i{
    color: black;
    margin: 0 12px;
}