:root {
  --dark: #040404;
  --light: #f5f5f5;
  --primary: #00aaff;
  --heading: #F9F9F9;
  --bodyColor: #8E8E8E;
  --gradient1: radial-gradient(51% 51% at 49% 48%, rgba(253, 253, 253, .05) 0%, rgba(253, 253, 253, 0) 100%);
  --gradient2: linear-gradient(.0000026635723315848736deg, rgba(0, 170, 255, .05) 0%, rgba(182, 102, 195, .05000000074505806) 100%);
  --gradient3: linear-gradient(270deg, rgba(0, 170, 255, 0.05) 0%, rgba(0, 170, 255, 0.04) 100%);
  --gradient5: linear-gradient(-90deg, rgba(0, 170, 255, 0.04) 0%, rgba(0, 170, 255, 0.02) 100%);
  --gradient6: linear-gradient(70deg, rgba(0, 170, 255, 0.04) 0%, rgba(0, 170, 255, 0.02) 100%);
  --gradient7: linear-gradient(180deg, rgba(0, 163, 255, .05) 0%, rgba(0, 163, 255, .019999999552965164) 100%);
  --gradient8: linear-gradient(78deg, rgb(0 170 255 / 4%), rgb(0 170 255 / 2%));
  --gradient9: linear-gradient(-89.99999881140911deg, rgba(0, 170, 255, .04) 0%, rgba(0, 170, 255, .019999999552965164) 100%);
  --gradient10: linear-gradient(180deg, rgba(0, 163, 255, 0.04) 0%, rgba(0, 163, 255, 0.02) 100%);
  --gradient11: linear-gradient(180deg, rgba(0, 163, 255, 0.05) 0%, rgba(0, 163, 255, 0.02) 100%);
  --gradient12: linear-gradient(270deg, rgba(0, 170, 255, 0.04) 0%, rgba(0, 170, 255, 0.02) 100%);
  --gradient13: linear-gradient(270deg, rgba(0, 170, 255, 0.02) 0%, rgba(0, 170, 255, 0.00) 100%);
  --gradient14: linear-gradient(-90deg, rgba(0, 170, 255, 0.02) 0%, rgba(0, 170, 255, 0) 100%);
  --gradient15: linear-gradient(0deg, rgba(0, 163, 255, 0.04) 0%, rgb(182 103 195 / 5%) 100%);
  --gradient16: linear-gradient(to right, rgba(0, 170, 255, 0.01), rgba(0, 170, 255, 0.05));
  --gradient18: linear-gradient(to right, rgba(0, 170, 255, 0.01), rgba(0, 170, 255, 0.05)) transparent;
}

* {
  scroll-behavior: smooth;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--dark);
  color: var(--heading);
}

html::-webkit-scrollbar {
  width: 12px;
}

html::-webkit-scrollbar-track {
  background-color: transparent;
}

html::-webkit-scrollbar-thumb {
  background: #03131a;
  border-radius: 25px;
}