nav {
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 28px;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 10;
    overflow: hidden;
}

#navDarkTheme{
    background-color: #030d12;
    color: white;
}
#navLightTheme{
    background-color: #f2f7fa;
    color: black;
}

.homeLogo {
    width: 180px;
}
.homeImage img {
    width: 60%;
}

.navButtons{
    display: flex;
    flex-wrap: nowrap;
    height: 100%;
}

.navList {
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
}
.navList li {
    margin: 0 8px;
    display: flex;
    align-items: center;
}
.navList li a{
    cursor: pointer;
    font-size: large;
    text-decoration: none;
    /* color: #F9F9F9; */
    transition: color 0.3s;
    width: 135px;
    text-align: center;
    height: 69px;
    line-height: 70px;
}

#listDarkTheme li a{
    color: white;
}
#listLightTheme li a{
    color: black;
}
.navList li a img {
    width: 30px;
    margin: 0 5px;
    vertical-align: middle;
}

.navList li a span{
    position: relative;
    top: 6px;
}



@media screen and (max-width: 900px) {
    .navList li a span{
        display: none;
    }
    .homeLogo{
        display: none;
    }
    .navList li a{
        width: 7rem;
    }
    nav{
        padding: 0;
    }
}

.activeLink{
    box-sizing: border-box;
    border-bottom: 2px solid var(--primary) ;
    border-radius: 1px;
}

.switchThemeButton{
    display: grid;
    place-items: center;
}

.theme{
    display: flex;
    align-items: center;
    -webkit-tap-highlight-color: transparent;
}

.themeIcon {
    pointer-events: none;
    transition: transform 0.3s ease-in-out;
}

.themeIcon,
.themeToggle {
    z-index: 1;
}

.themeIcon,
.themeIconPart {
    position: absolute;
}

.themeIcon {
    display: block;
    top: 0.75em;
    left: 0.75em;
    width: 1.5em;
    height: 1.5em;
}

.themeIconPart {
    border-radius: 50%;
    box-shadow: 0.4em -0.4em 0 0.5em hsl(0, 0%, 100%) inset;
    top: calc(50% - 0.5em);
    left: calc(50% - 0.5em);
    width: 1em;
    height: 1em;
    transition: box-shadow 0.3s ease-in-out,
        opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
    transform: scale(0.5);
}

.themeIconPart ~ .themeIconPart {
    background-color: hsl(0, 0%, 100%);
    border-radius: 0.05em;
    box-shadow: none;
    top: 50%;
    left: calc(50% - 0.05em);
    transform: rotate(0) translateY(0.5em);
    transform-origin: 50% 0;
    width: 0.1em;
    height: 0.2em;
}

.themeIconPart:nth-child(3) {
    transform: rotate(45deg) translateY(0.5em);
}

.themeIconPart:nth-child(4) {
    transform: rotate(90deg) translateY(0.5em);
}

.themeIconPart:nth-child(5) {
    transform: rotate(135deg) translateY(0.5em);
}

.themeIconPart:nth-child(6) {
    transform: rotate(180deg) translateY(0.5em);
}

.themeIconPart:nth-child(7) {
    transform: rotate(225deg) translateY(0.5em);
}

.themeIconPart:nth-child(8) {
    transform: rotate(270deg) translateY(0.5em);
}

.themeIconPart:nth-child(9) {
    transform: rotate(315deg) translateY(0.5em);
}

.themeLabel,
.themeToggle,
.themeToggleWrap {
    position: relative;
}

.themeToggle,
.themeToggle::before {
    display: block;
}

.themeToggle {
    background-color: hsl(48, 90%, 85%);
    border-radius: 25% / 50%;
    box-shadow: 0 0 0 0.125em hsla(223, 90%, 55%, 0);
    padding: 0.25em;
    width: 6em;
    height: 3em;
    appearance: none;
    -webkit-appearance: none;
    cursor: pointer;
    transition: background-color 0.3s ease-in-out,
        box-shadow 0.15s ease-in-out, transform 0.3s ease-in-out;
}

.themeToggle::before {
    background-color: hsl(48, 90%, 55%);
    border-radius: 50%;
    content: '';
    width: 2.5em;
    height: 2.5em;
    transition: background-color 0.3s ease-in-out,
        transform 0.3s ease-in-out;
}

.themeToggle:focus {
    outline: transparent;
}

.themeToggle:checked {
    background-color: hsl(198, 90%, 15%);
}

.themeToggle:checked::before,
.themeToggle:checked ~ .themeIcon {
    transform: translateX(3em);
    left: 6px;
}

.themeToggle:checked::before {
    background-color: hsl(198, 90%, 55%);
}

.themeToggle:checked~.themeIcon .themeIconPart:nth-child(1) {
    transform: scale(1);
    box-shadow: 0.2em -0.2em 0 0.2em hsl(0, 0%, 100%) inset;
}

.themeToggle:checked~.themeIcon .themeIconPart~.themeIconPart {
    opacity: 0;
}

.themeToggle:checked~.themeIcon .themeIconPart:nth-child(2) {
    transform: rotate(45deg) translateY(0.8em);
}

.themeToggle:checked~.themeIcon .themeIconPart:nth-child(3) {
    transform: rotate(90deg) translateY(0.8em);
}

.themeToggle:checked~.themeIcon .themeIconPart:nth-child(4) {
    transform: rotate(135deg) translateY(0.8em);
}

.themeToggle:checked~.themeIcon .themeIconPart:nth-child(5) {
    transform: rotate(180deg) translateY(0.8em);
}

.themeToggle:checked~.themeIcon .themeIconPart:nth-child(6) {
    transform: rotate(225deg) translateY(0.8em);
}

.themeToggle:checked~.themeIcon .themeIconPart:nth-child(7) {
    transform: rotate(270deg) translateY(0.8em);
}

.themeToggle:checked~.themeIcon .themeIconPart:nth-child(8) {
    transform: rotate(315deg) translateY(0.8em);
}

.themeToggle:checked~.themeIcon .themeIconPart:nth-child(9) {
    transform: rotate(360deg) translateY(0.8em);
}

.themeToggleWrap {
    margin: 0 0.75em;
}

.bgBlack {
    background-color: black;
    width: 500px;
    height: 500px;
}