main {
  position: relative;
  float: right;
  top: 90px;
  margin: 0 8px;
  width: 87vw;
}

i{
  color: var(--primary);
}

/* Style for Intro Component  */

.introSection {
  min-height: 350px;
  display: flex;
  justify-content: space-between;
  padding: 22px;
  backdrop-filter: blur(0px);
  background: var(--gradient7);
  border: .75px solid rgba(252, 252, 252, .05);
}

.userImage {
  width: 25%;
  background: url('../../assets/static/user.png') center/cover no-repeat;
  mix-blend-mode: lighten;
}

.quickDetails {
  width: 73%;
  display: flex;
  flex-direction: column;
}

@media screen and (max-width: 900px) {
  main {
    width: 98vw;
  }
  .userImage {
    display: none;
  }
  .quickDetails {
    width: 100%;
  }
}

.quickLink {
  border: .75px solid rgba(252, 252, 252, .05);
  -webkit-backdrop-filter: blur(30px);
  backdrop-filter: blur(30px);
  background: var(--gradient8);
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  margin-bottom: 16px;
  gap: 14px;
  padding: 14px;
}

.quickLink div {
  background: var(--gradient2);
  border: .75px solid rgb(252 252 252 / 5%);
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}

.quickLink div p{
  margin: 12px 0;
  font-size: 0.9rem;
}

.status span, .currentTime i{
  margin-right: 18px;
}
.contactMe span{
  margin-left: 6px;
}

.contactMe p a{
  text-decoration: none;
  color: white;
}

.contactMe p:hover a,.contactMe p:hover{
  color: var(--primary);
}

.available {
  width: 6px;
  height: 6px;
  background: #00A34C;
  border-radius: 50%;
  position: relative;
  flex: none;
}

@media screen and (max-width: 650px) {
  .quickLink div p{
    margin: 8px 0;
    font-size: 0.5rem;
  }
  .available{
    width: 3px;
    height: 3px;
  }
  .currentTime i{
    font-size: 0.7rem;
    margin: 3px;
  }
}

.available::before {
  content: '';
  background: rgb(0, 163, 76);
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  animation: onlineAnim 1s infinite linear;
  animation-duration: 1s;
  animation-timing-function: linear;
  animation-delay: 0s;
  animation-iteration-count: infinite;
  animation-direction: normal;
  animation-fill-mode: none;
  animation-play-state: running;
  animation-name: onlineAnim;
}

@keyframes onlineAnim {
  0% {
    transform: scale(1);
  }

  100% {
    transform: scale(5);
    opacity: 0;
  }
}

.aboutMe {
  display: flex;
  justify-content: space-between;
}

.aboutMe .intro, .aboutMe .address{
  background: var(--gradient9);
  border: .75px solid rgb(252 252 252 / 5%);
  padding: 22px;
}

.intro {
  width: 60%;
  backdrop-filter: blur(30px);
}

.intro p{
  font-size: 13px;
  line-height: 26.7px;
  letter-spacing: 0.23px;
  margin-bottom: 8px;
  font-weight: 300;
  text-align: justify;
  color: rgb(188 188 188 / 70%);
  user-select: none;
}

.intro h2{
  font-size: 30px;
  font-weight: 600;
  margin: 8px 0;
  letter-spacing: -0.75px;
  line-height: 1;
}

.address {
  width: 39%;
  backdrop-filter: blur(0px);
  position: relative;
  overflow: hidden;
}

.addressContainer {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 12px;
}

.addressContainer div {
  background: var(--gradient2);
  border: .75px solid rgb(252 252 252 / 5%);
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.9rem;
  padding: 12px 22px;
}

.country i {
  margin-right: 8px;
}

.circle {
  position: absolute;
  width: 163%;
  height: 163%;
  bottom: -100%;
  right: -100%;
  border-radius: 50%;
  background-color: var(--dark);
  box-shadow: 0px 1px 20px 0px var(--primary);
}

@media screen and (max-width: 600px) {
  .aboutMe {
    flex-direction: column;
  }
  .intro {
    width: 100%;
  }
  .address {
    width: 100%;
  }
  .circle {
    display: none;
  }
}


/* Style for Skill Component  */

.skillSection {
  margin: 22px 0;
  padding: 22px;
  backdrop-filter: blur(0px);
  background: var(--gradient7);
  border: .75px solid rgba(252, 252, 252, .05);
}

.skillHeader {
  border: .75px solid rgb(252 252 252 / 5%);
  backdrop-filter: blur(30px);
  background: var(--gradient8);
  display: flex;
  align-items: center;
  padding: 14px;
}

.headerContent p{
  font-size: 0.9rem;
  margin: 8px;
}

.headerContent:nth-child(1){
  border: .75px solid rgb(252 252 252 / 5%);
  background: var(--gradient2);
}

.skillContentContainer {
  box-shadow: rgba(0, 0, 0) 0px 1px 3px 0px, rgba(27, 31, 35,0.05) 0px 0px 0px 1px;
  margin-top: 22px;
}

.skillContent {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 12px;
}

.box {
  position: relative;
  border: .75px solid rgb(252 252 252 / 5%);
  background: var(--gradient8);
  backdrop-filter: blur(30px);
  padding: 14px 0;
  box-sizing: border-box;
}

.box .content {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.box .content .percent {
  position: relative;
  width: 150px;
  height: 150px;
}

.box .content .percent::before {
  content: attr(data-text);
  position: absolute;
  inset: 20px;
  background: var(--gradient9);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--primary);
  font-size: 1.2rem;
  letter-spacing: 0.1rem;
  text-transform: uppercase;
}

@media screen and (max-width: 700px) {
  .skillContent {
    grid-template-columns: repeat(2, 1fr);
  }
  .headerContent p,.skillHeader span{
    font-size: 0.5rem;
  }
  .box .content .percent::before{
    font-size: 1rem;
  }
}

.box .content .percent svg {
  position: relative;
  width: 150px;
  height: 150px;
  transform: rotate(270deg);
}

.box .content .percent svg circle {
  width: 100%;
  height: 100%;
  fill: transparent;
  stroke-width: 3;
  stroke: rgb(128, 128, 128, 0.1);
  transform: translate(5px, 5px);
}

.box .content .percent svg circle:nth-child(2) {
  stroke: var(--primary);
  stroke-dasharray: 440;
  stroke-dashoffset: calc(440 - (440 * var(--num)) / 100);
  opacity: 0;
  animation: fadeIn 1s linear forwards;
  animation-delay: 2.5s;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.box .content .percent .dot {
  position: absolute;
  inset: 5px;
  z-index: 10;
  animation: animateDot 2s linear forwards;
}

@keyframes animateDot {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(calc(3.6deg * var(--num)));
  }
}

.box .content .percent .dot::before {
  content: '';
  position: absolute;
  top: -7px;
  left: 50%;
  transform: translateX(-50%);
  width: 14px;
  height: 14px;
  background: var(--primary);
  border-radius: 50%;
}

.box .content .number {
  position: relative;
  inset: 0;
  opacity: 0;
  animation: fadeIn 1s linear forwards;
  animation-delay: 2.5s;
}

.box .content .number h2 {
  margin: 8px 0;
  font-size: 1.5rem;
  color: #555;
}

.box .content .number h2 span {
  font-weight: 300;
  font-size: 1rem;
}

/* Style for background section */

.background {
  backdrop-filter: blur(0px);
  background: var(--gradient7);
  border: .75px solid rgba(252, 252, 252, .05);
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 12px;
  padding: 22px;
}

@media screen and (max-width: 900px) {
  .background {
    grid-template-columns: repeat(1, 1fr);
  }
}

.education,
.experience {
  box-sizing: border-box;
  border: .75px solid rgb(252 252 252 / 5%);
  background: var(--gradient9);
  backdrop-filter: blur(0px);
  padding: 14px;
}

.educationHeader,
.experienceHeader {
  border: .75px solid rgb(252 252 252 / 5%);
  background: var(--gradient2);
  width: fit-content;
  margin: 4px 0 8px 0;
  padding: 8px 12px;
}

.eduContent{
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  margin-left: 14px;
}

.eduContentContainer{
  display: flex;
  align-items: center;
  box-sizing: border-box;
  margin: 12px 24px;
}

.eduLogo{
  margin-right: 8px;
}

.eduInfo p{
  margin: 4px;
  font-size: small;
  color: rgb(188 188 188 / 70%);
}

.eduInfo h3{
  margin: 4px;
}

.fontLarger{
  font-size: large;
}

.trackPath{
  position: absolute;
  left: 0;
  width: 0;
  border: 1px dotted rgba(71, 71, 71, 0.2);
  height: 65%;
}

.trackStart{
  width: 10px;
  height: 10px;
  background-color: var(--primary);
  filter: blur(1.5px);
  border-radius: 50%;
  position: absolute;
  top: -5px;
  left: -5px;
}

.trackStop{
  filter: blur(1.5px);
  width: 10px;
  height: 10px;
  background-color: var(--primary);
  border-radius: 50%;
  position: absolute;
  bottom: -5px;
  left: -5px;
}

/* Style for contact section */

.connect {
  margin: 22px 0;
  padding: 22px;
  border: .75px solid rgba(252, 252, 252, 0.05);
  background: var(--gradient7);
  backdrop-filter: blur(0px);
}

.connectHeader {
  border: .75px solid rgb(252 252 252 / 5%);
  background: var(--gradient8);
  backdrop-filter: blur(30px);
  display: flex;
  align-items: center;
  padding: 14px;
}

.connectHeader div {
  border: .75px solid rgb(252 252 252 / 5%);
  background: var(--gradient2);
  padding: 8px 12px;
}

.connectContent {
  display: flex;
  justify-content: space-between;
  margin-top: 18px;
}

.form {
  background: var(--gradient9);
  backdrop-filter: blur(30px);
  width: 60%;
  display: flex;
  flex-direction: column;
  border: .75px solid rgb(252 252 252 / 5%);
  padding: 33px;
}

.form input,
.form textarea,
.form button {
  margin-bottom: 24px;
  background-color: transparent;
  border: .75px solid rgb(252 252 252 / 5%);
  color: gray;
  background: var(--gradient16);
  padding: 13px;
}

.form input:focus-visible,.form textarea:focus-visible{
  outline: 0.5px solid rgba(0, 170, 255, 0.5);
}

.form input {
  height: 45px;
}

.form textarea {
  max-width: 99%;
  height: 100px;
}

.form button {
  height: 50px;
  font-weight: 900px;
}

.connectInfoBox {
  width: 38%;
  border: .75px solid rgb(252 252 252 / 5%);
  padding: 22px;
  box-sizing: border-box;
  background: var(--gradient9);
  backdrop-filter: blur(0px);
}

@media screen and (max-width: 900px) {
  .connectContent {
    flex-direction: column;
  }
  .form {
    width: 100%;
    margin-bottom: 12px;
  }
  .connectInfoBox {
    width: 100%;
  }
}

.addressHead{
  width: fit-content;
  border: .75px solid rgb(252 252 252 / 5%);
  padding: 8px 12px;
  background: var(--gradient2);
}

.addressContentContainer{
  margin-top: 14px;
  max-height: 85%;
  border: .75px solid rgb(252 252 252 / 5%);
  padding: 32px;
}

.addressContentContainer div:nth-child(1){
  margin-bottom: 18px;
}

.addressInfoContainer{
  display: flex;
  position: relative;
}

.connectInfoHead{
  width: 30%;
}
.addressIcon{
  background: linear-gradient(-90deg, rgba(0, 170, 255, 0.02) 0%, rgba(0, 170, 255, 0) 100%);
  backdrop-filter: blur(15px);
  border: .75px solid rgb(252 252 252 / 5%);
  width: 60%;
  height: 50px;
  margin: auto;
  text-align: center;
  line-height: 50px;
}

.addressinfo{
  background: var(--gradient9);
  border: .75px solid rgb(252 252 252 / 5%);
  width: 70%;
  padding: 0 8px 4px 12px;
  overflow: auto;
}
.addressinfo p:not(:first-child){
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.5);
  margin: 4px 0;
}
.addressinfo::before{
  content: "";
  border-bottom: 1px dashed rgba(0, 170, 255, .2);
  border-left: 1px dashed rgba(0, 170, 255, .2);
  position: absolute;
  top: 52px;
  right: 70%;
  height: 24px;
  width: 44px;
  box-sizing: border-box;
  border-radius: inherit;
  pointer-events: none;
}

/* Footer */

footer {
  border-top: 1px solid rgba(252, 252, 252, 0.1);
  display: flex;
  justify-content: space-between;
  margin-top: 14px;
  position: relative;
  height: 50px;
  line-height: 50px;
  color: rgba(255, 255, 255, 0.5);
  font-size: small;
  padding: 0 12px;
}

footer div span{
  font-size: 1rem;
  margin: 0 6px;
}

footer a{
  text-decoration: none;
  color: white;
}

a:hover{
  color: var(--primary);
}

/* Footer for mobile view */

.mobileFooter{
  display: none;
  border-top: 1px solid rgba(255, 255, 255, 0.5);
}

@media screen and (max-width: 700px) {
  .mobileFooter{
    display: block;
  }
  footer div:nth-child(1){
    visibility: hidden;
  }
}

.mobileFooter ul{
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: space-around;
  font-size: 1.8rem;
}

.mobileFooter p{
  text-align: center;
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.5);
  margin: 32px 0;
  font-weight: 300;
}

.mobileFooter i{
  color: rgba(255, 255, 255, 0.8);
}