.projectSection{
    position: relative;
    float: right;
    top: 90px;
    margin: 0 8px;
    width: 87vw;
}

@media screen and (max-width: 900px) {
    .projectSection {
        width: 96vw;
    }
}

.projectContainer{
    border: .75px solid rgba(252, 252, 252 , .05);
    backdrop-filter: blur(0px);
    background: var(--gradient7);
    padding: 22px;
    margin-bottom: 14px;
}

.projectHeader{
    border: .75px solid rgb(252 252 252 / 5%);
    backdrop-filter: blur(30px);
    background: var(--gradient8);
    padding: 14px;
}

.projectHeaderContent{
    width: 100px;
    text-indent: 1cap;
    background: var(--gradient2);
    border: .75px solid rgb(252 252 252 / 5%);
}

.projectHeaderContent p{
    margin: 12px 0;
}

.projectCard{
    display: flex;
    flex-direction: column;
    align-items: end;
    background: var(--gradient9);
    border: .75px solid rgba(252, 252, 252, .05);
    margin: 18px 0;
}

.projectContent{
    padding: 12px;
    display: flex;
    justify-content: space-between;
    background: var(--gradient9);
}

.aboutProject{
    width: 60%;
    position: relative;
}

.aboutProject > h3{
    font-size: 2.5rem;
    margin: 8px 0;
}

.aboutProject > p{
    width: 100%;
    height: 40%;
    text-align: justify;
    line-height: 24px;
    overflow: auto;
    color: rgba(245, 245, 245, 0.5);
}

.projectContent > div{
    padding: 12px;
}

.projectLinks{
    display: flex;
    justify-content: end;
}

.projectLinks > div{
    width: 150px;
    height: 55px;
}

.projectLinks > div i{
    font-size: 2rem;
    margin: 0px 8px;
    position: relative;
    top: 4px;
}

.projectLinks div a{
    text-decoration: none;
    color: white;
    line-height: 55px;
    margin: 0px 12px;
    text-align: center;
    font-size: 1.2rem;
}

.projectLinks div a:hover{
    color: var(--primary);
}

.projectGif{
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.projectViewer{
    width: 330px;
    height: 244px;
    background: url('../../assets/static/projects/desktopFrame.png') no-repeat;
    background-size: 100% 100%;
    position: relative;
    z-index: 1;
    filter: drop-shadow(0px 5px 34px rgba(225,225, 225, 0.3));
}

@media screen and (max-width: 900px) {
    .projectCard{
        align-items: center;
    }
    .projectContent{
        flex-direction: column;
        padding: 0px;
    }
    .aboutProject{
        width: auto;
    }
    .projectGif{
        justify-content: start;
    }
    .projectLinks{
        position: relative;
        /* flex-direction: column; */
        /* bottom: -80%;
        right: 22px; */
        justify-content: center;
    }
}

.projectViewer img{
    width: 255px;
    position: absolute;
    top: 45px;
    left: 38px;
    border-top-left-radius: 6px;
    border-top-right-radius: 6px;
    z-index: -1;
}
